import { Link } from 'react-router-dom';
import { useState } from 'react';
import { FOOTER_QUICK_LINKS, FOOTER_SUPPORT_LINKS, COMPANY_INFO, SOCIAL_LINKS } from '../../utils/constants.js';

import logo from '../../assets/images/logo/foot.webp';

const Footer = () => {
    const currentYear = new Date().getFullYear();
    const [hoveredSocial, setHoveredSocial] = useState(null);

    const socialLinksArray = [
        {
            name: 'LinkedIn',
            icon: (
                <svg className="w-full h-full" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
                </svg>
            ),
            url: SOCIAL_LINKS.linkedin,
        },
        {
            name: 'Twitter',
            icon: (
                <svg className="w-full h-full" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M23.953 4.57a10 10 0 01-2.825.775 4.958 4.958 0 002.163-2.723c-.951.555-2.005.959-3.127 1.184a4.92 4.92 0 00-8.384 4.482C7.69 8.095 4.067 6.13 1.64 3.162a4.822 4.822 0 00-.666 2.475c0 1.71.87 3.213 2.188 4.096a4.904 4.904 0 01-2.228-.616v.06a4.923 4.923 0 003.946 4.827 4.996 4.996 0 01-2.212.085 4.936 4.936 0 004.604 3.417 9.867 9.867 0 01-6.102 2.105c-.39 0-.779-.023-1.17-.067a13.995 13.995 0 007.557 2.209c9.053 0 13.998-7.496 13.998-13.985 0-.21 0-.42-.015-.63A9.935 9.935 0 0024 4.59z"/>
                </svg>
            ),
            url: SOCIAL_LINKS.twitter,
        },
        {
            name: 'Facebook',
            icon: (
                <svg className="w-full h-full" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
                </svg>
            ),
            url: SOCIAL_LINKS.facebook,
        },
        {
            name: 'Instagram',
            icon: (
                <svg className="w-full h-full" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12 0C8.74 0 8.333.015 7.053.072 5.775.132 4.905.333 4.14.63c-.789.306-1.459.717-2.126 1.384S.935 3.35.63 4.14C.333 4.905.131 5.775.072 7.053.012 8.333 0 8.74 0 12s.015 3.667.072 4.947c.06 1.277.261 2.148.558 2.913.306.788.717 1.459 1.384 2.126.667.666 1.336 1.079 2.126 1.384.766.296 1.636.499 2.913.558C8.333 23.988 8.74 24 12 24s3.667-.015 4.947-.072c1.277-.06 2.148-.262 2.913-.558.788-.306 1.459-.718 2.126-1.384.666-.667 1.079-1.335 1.384-2.126.296-.765.499-1.636.558-2.913.06-1.28.072-1.687.072-4.947s-.015-3.667-.072-4.947c-.06-1.277-.262-2.149-.558-2.913-.306-.789-.718-1.459-1.384-2.126C21.319 1.347 20.651.935 19.86.63c-.765-.297-1.636-.499-2.913-.558C15.667.012 15.26 0 12 0zm0 2.16c3.203 0 3.585.016 4.85.071 1.17.055 1.805.249 2.227.415.562.217.96.477 1.382.896.419.42.679.819.896 1.381.164.422.36 1.057.413 2.227.057 1.266.07 1.646.07 4.85s-.015 3.585-.074 4.85c-.061 1.17-.256 1.805-.421 2.227-.224.562-.479.96-.899 1.382-.419.419-.824.679-1.38.896-.42.164-1.065.36-2.235.413-1.274.057-1.649.07-4.859.07-3.211 0-3.586-.015-4.859-.074-1.171-.061-1.816-.256-2.236-.421-.569-.224-.96-.479-1.379-.899-.421-.419-.69-.824-.9-1.38-.165-.42-.359-1.065-.42-2.235-.045-1.26-.061-1.649-.061-4.844 0-3.196.016-3.586.061-4.861.061-1.17.255-1.814.42-2.234.21-.57.479-.96.9-1.381.419-.419.81-.689 1.379-.898.42-.166 1.051-.361 2.221-.421 1.275-.045 1.65-.06 4.859-.06l.045.03zm0 3.678c-3.405 0-6.162 2.76-6.162 6.162 0 3.405 2.76 6.162 6.162 6.162 3.405 0 6.162-2.76 6.162-6.162 0-3.405-2.76-6.162-6.162-6.162zM12 16c-2.21 0-4-1.79-4-4s1.79-4 4-4 4 1.79 4 4-1.79 4-4 4zm7.846-10.405c0 .795-.646 1.44-1.44 1.44-.795 0-1.44-.646-1.44-1.44 0-.794.646-1.439 1.44-1.439.793-.001 1.44.645 1.44 1.439z"/>
                </svg>
            ),
            url: SOCIAL_LINKS.instagram,
        },
        {
            name: 'GitHub',
            icon: (
                <svg className="w-full h-full" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"/>
                </svg>
            ),
            url: SOCIAL_LINKS.github,
        },
    ];

    return (
        <footer className="relative bg-neutral-900 overflow-hidden">

            <div className="absolute inset-0 pointer-events-none opacity-5">
                <div
                    className="absolute inset-0"
                    style={{
                        backgroundImage: `linear-gradient(to right, rgba(255,255,255,0.03) 1px, transparent 1px),
                            linear-gradient(to bottom, rgba(255,255,255,0.03) 1px, transparent 1px)`,
                        backgroundSize: '80px 80px',
                    }}
                />
            </div>

            <div className="absolute top-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-blue-600/30 to-transparent" />

            <div className="container-custom relative z-10">

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-12 lg:gap-16 py-16 sm:py-20">

                    <div className="space-y-6">
                        <Link to="/" className="inline-block group">
                            <div className="flex justify-center md:justify-start">
                                <img
                                    src={logo}
                                    alt="Punchay Logo"
                                    className="h-16 w-auto transition-transform duration-300 group-hover:scale-105"
                                    onError={(e) => {
                                        e.target.style.display = 'none';
                                        e.target.nextSibling.style.display = 'flex';
                                    }}
                                />
                                <div className="hidden items-center gap-3">
                                    <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-600 to-indigo-600
                      flex items-center justify-center">
                                        <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5}
                                                  d="M13 10V3L4 14h7v7l9-11h-7z" />
                                        </svg>
                                    </div>
                                    <span className="text-2xl font-black text-white">Punchay</span>
                                </div>
                            </div>
                        </Link>

                        <p className="text-neutral-400 leading-relaxed text-center md:text-left">
                            Construyendo soluciones tecnolgicas inclusivas que transforman vidas
                            y abren oportunidades para todos.
                        </p>

                        <div className="flex justify-center md:justify-start">

                            <a
                            href={`mailto:${COMPANY_INFO.email}`}
                            className="group inline-flex items-center gap-3 text-neutral-400
                            hover:text-white transition-colors duration-300"
                            >
                            <div className="w-10 h-10 rounded-lg bg-neutral-800 border border-neutral-700
                      flex items-center justify-center flex-shrink-0
                      group-hover:bg-blue-600 group-hover:border-blue-600
                      transition-all duration-300">
                                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                                          d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                                </svg>
                            </div>
                            <span className="font-medium">{COMPANY_INFO.email}</span>
                        </a>
                    </div>
                </div>

                    <div>
                        <h3 className="text-white font-bold text-lg mb-6">
                            Enlaces Rpidos
                        </h3>
                        <ul className="space-y-3">
                            {FOOTER_QUICK_LINKS.map((link) => (
                                <li key={link.path}>
                                    <Link
                                        to={link.path}
                                        className="group inline-flex items-center gap-2 text-neutral-400
                             hover:text-white transition-all duration-300"
                                    >
                                        <div className="w-1 h-1 rounded-full bg-neutral-600
                                  group-hover:bg-blue-600 group-hover:scale-150
                                  transition-all duration-300" />
                                        <span className="relative">
                      {link.name}
                                            <span className="absolute -bottom-1 left-0 w-0 h-px bg-blue-600
                                     group-hover:w-full transition-all duration-300" />
                    </span>
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </div>

                    <div>
                        <h3 className="text-white font-bold text-lg mb-6">
                            Donaciones y Apoyo
                        </h3>
                        <ul className="space-y-3">
                            {FOOTER_SUPPORT_LINKS.map((link) => (
                                <li key={link.path}>
                                    {link.external ? (

                                        <a
                                        href={link.path}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="group inline-flex items-center gap-2 text-neutral-400
                                        hover:text-white transition-all duration-300"
                                        >
                                        <div className="w-1 h-1 rounded-full bg-neutral-600
                                        group-hover:bg-blue-600 group-hover:scale-150
                                        transition-all duration-300" />
                                        <span className="relative">
                                    {link.name}
                                        <span className="absolute -bottom-1 left-0 w-0 h-px bg-blue-600
                                       group-hover:w-full transition-all duration-300" />
                                        </span>
                                        </a>
                                        ) : (
                                        <Link
                                        to={link.path}
                                    className="group inline-flex items-center gap-2 text-neutral-400
                               hover:text-white transition-all duration-300"
                                >
                                    <div className="w-1 h-1 rounded-full bg-neutral-600
                                    group-hover:bg-blue-600 group-hover:scale-150
                                    transition-all duration-300" />
                                    <span className="relative">
                        {link.name}
                                        <span className="absolute -bottom-1 left-0 w-0 h-px bg-blue-600
                                       group-hover:w-full transition-all duration-300" />
                      </span>
                                </Link>
                                )}
                        </li>
                        ))}
                    </ul>
                </div>
            </div>

            <div className="border-t border-neutral-800" />

            <div className="py-8 flex flex-col sm:flex-row items-center justify-between gap-8">

                <div className="flex items-center gap-4">
                    <span className="text-sm text-neutral-500 font-medium">Sguenos:</span>
                    <div className="flex items-center gap-3">
                        {socialLinksArray.map((social) => (

                            <a
                            key={social.name}
                            href={social.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            onMouseEnter={() => setHoveredSocial(social.name)}
                            onMouseLeave={() => setHoveredSocial(null)}
                            className="group relative w-10 h-10 rounded-lg bg-neutral-800 border border-neutral-700
                            flex items-center justify-center text-neutral-400
                            hover:bg-blue-600 hover:border-blue-600 hover:text-white
                            transition-all duration-300
                            focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 focus:ring-offset-neutral-900"
                            aria-label={`Visitar perfil de Punchay en ${social.name} - Se abre en nueva ventana`}
                            >
                            <div className="w-5 h-5 transition-transform duration-300 group-hover:scale-110">
                        {social.icon}
                            </div>

                        <div className={`absolute -top-10 left-1/2 -translate-x-1/2 whitespace-nowrap
                                px-3 py-1.5 rounded-lg bg-neutral-800 border border-neutral-700
                                text-xs font-medium text-white shadow-lg
                                transition-all duration-300
                                ${hoveredSocial === social.name
                            ? 'opacity-100 translate-y-0'
                            : 'opacity-0 translate-y-2 pointer-events-none'}`}>
                            {social.name}
                            <div className="absolute -bottom-1 left-1/2 -translate-x-1/2 w-2 h-2 rotate-45
                                  bg-neutral-800 border-r border-b border-neutral-700" />
                        </div>
                    </a>
                    ))}
                </div>
            </div>

            <div className="flex flex-col sm:flex-row items-center gap-4 text-sm text-neutral-500">
                <p className="flex items-center gap-2">
                    <span> {currentYear} Punchay</span>
                    <span className="hidden sm:inline"></span>
                    <span className="hidden sm:inline">Todos los derechos reservados</span>
                </p>
                <div className="flex items-center gap-4">
                    <Link
                        to="/politica-privacidad"
                        className="hover:text-white transition-colors duration-300"
                    >
                        Privacidad
                    </Link>
                    <span></span>
                    <Link
                        to="/terminos"
                        className="hover:text-white transition-colors duration-300"
                    >
                        Trminos
                    </Link>
                </div>
            </div>
        </div>
</div>
</footer>
);
};

export default Footer;